import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AreaListComponent } from './area-list/area-list.component';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { AddAreaComponent } from './add-area/add-area.component';
import { AreaService } from 'src/app/service/area.service';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { PdfViewerModule } from 'ng2-pdf-viewer';
import { AreaViewComponent } from './area-view/area-view.component';


const routes: Routes = [
  {
    path: "",
    component: AreaListComponent,
    data: { title: 'area_list' }
  },
  {
    path: "add",
    component: AddAreaComponent,
    data: { title: 'add_area' }
  },
  {
    path: 'edit/:id',
    component: AddAreaComponent,
    data: { title: 'edit_area' }
  },
  {
    path: 'view/:id',
    component: AreaViewComponent,
    data: { title: 'view_area' }
  },
]

@NgModule({
  declarations: [AreaListComponent, AddAreaComponent, AreaViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    PaginationModule,
    SharedModule,
    PdfViewerModule
  ], providers: [AreaService]
})
export class AreaModule { }
