import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AreaService } from 'src/app/service/area.service';

@Component({
  selector: 'app-area-view',
  templateUrl: './area-view.component.html',
})
export class AreaViewComponent implements OnInit {
  viewId: any;
  objectArray: any;
  popup: Window;

  constructor(
    private activatedRoute: ActivatedRoute,
    private areaservice: AreaService,
    private router: Router,
  ) {

    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }
  ngOnInit(): void {
    this.areaservice.getAreaById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.objectArray = response.data;

      }
    });
  }

  VoucherSourcetoPrint() {
    return (
      `
            <html>
            <head>
              <script>
                function step1() {
                  setTimeout('step2()', 10);
                }
                function step2() {
                  window.print(); window.close()
                }
              </script>
            </head>

            <body onload='step1()'>
              <h3>Area Name:- ${this.objectArray?.area_name}</h3>
              <h3>Area Code:- ${this.objectArray?.area_code}</h3>
              <img src='${this.objectArray?.area_bar_code_image}' />
            </body>
            </html>
      `
    );
  }
  VoucherPrint() {
    // alert()
    // let printContents = document.getElementById('sohil').innerHTML;
    // // let originalContents = document.body.innerHTML;
    // document.body.innerHTML = printContents;
    window.print();
    // document.close();

    // document.body.innerHTML = originalContents;

    // return false;
    // let pwa = window.open();
    // pwa.document.open();
    // pwa.document.write(this.VoucherSourcetoPrint());
    // pwa.document.close();
    // window.print();
  }


}

