import { Component, OnInit, ɵ_sanitizeStyle, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AreaService } from 'src/app/service/area.service';
import { CommonService } from 'src/app/service/common.service';
declare var document: any;
@Component({
  selector: 'app-add-area',
  templateUrl: './add-area.component.html'
})
export class AddAreaComponent implements OnInit {
  PageTitle = "Area"

  loadingState = false;
  addForm: FormGroup;
  barCodeFile: any;
  showLoader = false;
  formErrors = {
    area_code: null,
    apierror: null,
  };
  isEditing = false;
  editId = null;
  selectedFile: any;
  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private areaService: AreaService,
    private commonservice: CommonService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }

    this.addForm = this.fb.group({
      area_name: [null, Validators.compose([Validators.required])],
      area_code: [null, Validators.compose([Validators.required])],
      area_bar_code: [null]
    });
  }

  ngOnInit() {
    if (this.isEditing) {
      this.getEditObject()
    } else {
      this.getAreaBarcode();
    }
  }

  getAreaBarcode() {
    this.commonservice.getGenerateBarcode().subscribe((res) => {
      this.addForm.patchValue({ area_bar_code: res.type })
      const urlCreator = window.URL || window.webkitURL;
      document.getElementById('barCodeShow').src = urlCreator.createObjectURL(res);
      this.selectedFile = new File([res], 'noname.jpeg', { type: 'image/jpeg' })
    });
  }




  getEditObject() {
    this.areaService.getAreaById(this.editId).subscribe((response) => {
      if (response.success) {
        this.addForm.patchValue(response.data);
        this.barCodeFile = response.data.bar_code;
      } else {
        this.router.navigateByUrl('/manage/area');
      }
    });
  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('area_name', formData.value.area_name);
      data.append('area_code', formData.value.area_code);
      data.append('area_bar_code', formData.value.area_bar_code);
      if (this.selectedFile) {
        data.append('file', this.selectedFile);
      }
      if (this.isEditing) {
        this.areaService.editArea(this.editId, data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/area');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('area_code')) {
                this.formErrors['area_code'] = obj['area_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.areaService.addArea(data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/area');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('area_code')) {
                this.formErrors['area_code'] = obj['area_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }
}
